/* Global CSS variables */
:root {
  --app-bar-height: 50px;
  --editor-background: #e9e9e9;
  --editor-file-name-font-size: 16px;
  --editor-file-name-hovered-font-size: 16.5px;
  --header-background: #282c34;
  --mui-theme-spacing: 8px;
  /* 
  --secondary: #d87093;
  --secondary-light: rgb(214, 135, 161);
  --secondary-light-active: rgb(237, 159, 185);
  */
  --secondary: #448ee4;
  --secondary-light: rgb(78, 153, 238);
  --secondary-light-active: rgb(98, 173, 255);
  --sub-header-app-bar-height: 35px;
}

/* Key frames */
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* CSS */
.App {
  background-color: #bebebe;
  height: 100vh;
  overflow: hidden;
}

.App-header {
  align-items: center;
  background-color: var(--header-background);
  color: white;
  display: flex;
  flex-direction: row;
  font-size: calc(10px + 2vmin);
  height: calc(var(--app-bar-height) - 4px);
  justify-content: space-between;
  padding: 0 15px;
  text-align: center;
  user-select: none;
}
.App-header button {
  color: inherit;
}
.App-header label {
  color: inherit;
}
.App-header svg {
  color: inherit;
}
.App-header svg {
  height: 30px;
  width: 30px;
}
.App-header .MuiCollapse-wrapperInner {
  font-weight: 700 !important;
  white-space: nowrap;
}

.App-body {
  display: flex;
  height: calc(100vh - var(--app-bar-height));
}

.App-link {
  color: #61dafb;
}

.App-linear-progress {
  background-color: var(--header-background) !important;
  border-bottom: 1px solid var(--secondary);
  z-index: 1201 !important;
}
.App-linear-progress .MuiLinearProgress-bar {
  background-color: var(--secondary) !important;
}

.App-editor .MuiPaper-root {
  background-color: var(--editor-background);
  height: calc(100vh - var(--app-bar-height));
  overflow-y: hidden;
  top: var(--app-bar-height) !important;
}
.App-editor .editor {
  /* font-size: large; */
  height: calc(100vh - var(--app-bar-height));
  overflow: auto;
  text-align: initial;
}

.App-settings .MuiPaper-root {
  background-color: rgba(190, 190, 190, 0.7);
  height: calc(100vh - var(--app-bar-height));
  max-width: 20vw;
  top: var(--app-bar-height) !important;
}
.App-settings .settings span {
  font-size: calc(10px + 1vmin);
}
.editor-settings-section {
  background-color: rgba(136, 136, 136, 0.7);
  border-left: 4px solid var(--secondary-light);
  color: #fff;
}
.editor-settings-section .MuiStack-root {
  flex-grow: 1;
  padding: calc(var(--mui-theme-spacing) * 5);
}

.viewer {
  border: 1px solid var(--secondary);
  height: calc(100% - var(--sub-header-app-bar-height));
}

.viewer-info-box {
  position: absolute;
  right: 25px;
  top: calc(var(--app-bar-height) * 2);
  width: fit-content;
  height: 100px;
  margin: 1px;
}

.viewer-content-box {
  position: absolute;
  bottom: 0;
  margin: 1px;
}
.viewer-content-box div {
  font-size: larger;
  text-align: center;
  vertical-align: middle;
  backdrop-filter: blur(1px);
  background-color: rgba(190, 190, 190, 0.87);
  width: fit-content;
  padding: var(--mui-theme-spacing);
  height: fit-content;
  overflow: hidden;
}

.view-sub-header {
  background-color: var(--header-background);
  color: white;
  display: flex;
  height: var(--sub-header-app-bar-height);
}
.view-sub-header button,
svg {
  color: inherit !important;
}
.view-sub-header hr {
  background-color: white;
  margin: calc(var(--mui-theme-spacing) * 1) calc(var(--mui-theme-spacing) * 3);
}
.view-sub-header > .compiling .MuiSvgIcon-root {
  animation: rotate 1s linear infinite;
}
.view-sub-header > .recompile {
  background-color: var(--secondary);
  font-weight: 900;
  transition: 0.2s;
  user-select: none;
}
.view-sub-header > .recompile:hover {
  background-color: var(--secondary-light);
}
.view-sub-header > .recompile:active {
  background-color: var(--secondary-light-active);
}
.view-sub-header > .recompile .MuiSvgIcon-root {
  transition: 1s;
}

.editor-sub-header {
  background-color: var(--header-background);
  color: white;
  display: flex;
  height: var(--sub-header-app-bar-height);
  overflow-x: auto;
  overflow-y: hidden;
}
.editor-sub-header .MuiBox-root.selected {
  background-color: rgb(108, 108, 108);
  overflow: unset;
  text-overflow: unset;
  white-space: nowrap;
}
.editor-sub-header .MuiBox-root {
  align-items: center;
  border-left: 1px solid #222 !important;
  border-radius: 0;
  border-right: 1px solid #222 !important;
  border-top: unset !important;
  color: #fff;
  cursor: pointer;
  display: flex;
  font-family: "Courier New", Courier, monospace;
  font-size: var(--editor-file-name-font-size);
  font-weight: 700;
  overflow: hidden;
  padding: 0 calc(var(--mui-theme-spacing) * 1);
  text-overflow: ellipsis;
  transition: 0.2s;
  user-select: none;
  white-space: nowrap;
}
.editor-sub-header .MuiBox-root button {
  color: inherit;
  transition: inherit;
}
.editor-sub-header .MuiBox-root svg {
  opacity: 0;
}
.editor-sub-header .MuiBox-root:hover {
  background-color: rgb(122, 122, 122);
  overflow: unset;
  text-overflow: unset;
  white-space: nowrap;
}
.editor-sub-header .MuiBox-root:hover svg {
  opacity: 1;
}
.editor-sub-header .MuiBox-root.selected svg {
  opacity: 1;
}

.MuiBackdrop-root {
  backdrop-filter: blur(1px);
}
.filter-dialog-title {
  font-weight: 900 !important;
}

/* Presentation styles */
.presentation-mode .App-header {
  align-items: center;
  background-color: var(--header-background);
  color: #fff;
  display: flex;
  font-size: calc(10px + 2vmin);
  height: var(--sub-header-app-bar-height);
  justify-content: center;
  width: 100%;
  z-index: 1;
}

.presentation-mode .view-sub-header,
.presentation-mode .App-editor,
.presentation-mode .App-header .MuiBox-root {
  display: none;
}

.presentation-mode .App-viewer {
  margin-left: unset !important;
  padding: unset;
}

.presentation-mode .viewer {
  height: calc(100vh - var(--sub-header-app-bar-height));
  width: 100vw;
}
.presentation-mode .viewer-content-box div {
  font-size: x-large;
}
